@use 'sass:math';

@mixin t-gutters($padding: false){
  @if($padding == true){
    padding-left: $s-gutter--mobile;
    padding-right: $s-gutter--mobile;

    @include t-media-query($s-small-up) {
      padding-left: $s-gutter--small;
      padding-right: $s-gutter--small;
    }
    @include t-media-query($s-medium-up) {
      padding-left: $s-gutter--medium;
      padding-right: $s-gutter--medium;
    }
    @include t-media-query($s-xxlarge-up) {
      max-width: t-to-rem(2000);
      padding-left: $s-gutter--xxlarge;
      padding-right: $s-gutter--xxlarge;
      margin-left: auto;
      margin-right: auto;
    }
  } @else {
    margin-left: $s-gutter--mobile;
    margin-right: $s-gutter--mobile;
  
    @include t-media-query($s-small-up) {
      margin-left: $s-gutter--small;
      margin-right: $s-gutter--small;
    }
    @include t-media-query($s-medium-up) {
      margin-left: $s-gutter--medium;
      margin-right: $s-gutter--medium;
    }
    @include t-media-query($s-xxlarge-up) {
      $fullMinusGutters: 2000 - 2000 * math.div($s-gutter--medium * 2, 100%);
      max-width: t-to-rem($fullMinusGutters);
      margin-left: auto;
      margin-right: auto;
    }
  }
}

@mixin t-v-gutters($padding: false){
  @if($padding == true){
    padding-top: t-to-rem(40);
    padding-bottom: t-to-rem(40);
  
    @include t-media-query($s-medium-up) {
      padding-top: 8%;
      padding-bottom: 8%;
    }
    
    @include t-media-query($s-xxlarge-up) {
      padding-top: t-to-rem(160);
      padding-bottom: t-to-rem(160);
    }
  } @else {
    margin-top: t-to-rem(40);
    margin-bottom: t-to-rem(40);
  
    @include t-media-query($s-medium-up) {
      margin-top: 8%;
      margin-bottom: 8%;
    }
    
    @include t-media-query($s-xxlarge-up) {
      margin-top: t-to-rem(160);
      margin-bottom: t-to-rem(160);
    }
  }
}
