@import '../base.scss';

.c-featured-articles-grid {
  position: relative;
  padding-top: 1rem;
  z-index: 0;

  &__top{
    text-align: center;
    margin-bottom: 3rem;
  }

  &__inner{
    @include t-gutters;
  }

  &__article {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    margin-bottom: t-to-rem(53);
    
    &-tags {
      margin-bottom: t-to-rem(16);
    }

    &-tag {
      margin-right: t-to-rem(4);

      &:last-child{
        margin-right: unset;
      }
    }

    &-title{
      margin-bottom: t-to-rem(6);
    }
    &-author{
      margin-bottom: t-to-rem(30);
    }

    &-image {
      &-container{
        margin: auto 0 t-to-rem(20);
        width: 100%;
      }
    }
  }

  @include t-media-query($s-small-up) {
    padding-top: 3rem;

    &__articles {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      column-gap: t-to-rem(60);
      row-gap: t-to-rem(96);
    }

    &__article{
      &-tags{
        margin-bottom: t-to-rem(22);
      }
      &-title{
        margin-bottom: t-to-rem(26);
      }
      &-auther{
        margin-bottom: t-to-rem(50);
      }
      &-image-container{
        margin-bottom: t-to-rem(44);
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    padding-top: t-to-rem(264);
  }
}